.advanced-dashboard {
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
  border-radius: 16px;
  max-width: 1400px;
  margin: 20px auto;
  position: relative;
  overflow: hidden;
  box-shadow: 0 20px 40px #0000001a;
}

.dashboard-header {
  color: #fff;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  justify-content: space-between;
  align-items: center;
  padding: 20px 30px;
  display: flex;
}

.dashboard-header h2 {
  margin: 0;
  font-size: 1.8em;
  font-weight: 600;
}

.dashboard-controls {
  gap: 12px;
  display: flex;
}

.control-btn {
  color: #fff;
  cursor: pointer;
  backdrop-filter: blur(10px);
  background: #ffffff26;
  border: 1px solid #ffffff4d;
  border-radius: 8px;
  padding: 8px 16px;
  font-size: .9em;
  transition: all .3s;
}

.control-btn:hover {
  background: #ffffff40;
  transform: translateY(-2px);
}

.dashboard-navigation {
  background: #fff;
  border-bottom: 1px solid #e2e8f0;
  padding: 0 30px;
}

.tab-navigation {
  gap: 0;
  display: flex;
}

.tab-btn {
  cursor: pointer;
  color: #64748b;
  background: none;
  border: none;
  border-bottom: 3px solid #0000;
  padding: 16px 24px;
  font-size: .95em;
  font-weight: 500;
  transition: all .3s;
  position: relative;
}

.tab-btn:hover {
  color: #3b82f6;
  background: #3b82f60d;
}

.tab-btn.active {
  color: #3b82f6;
  background: #3b82f60d;
  border-bottom-color: #3b82f6;
}

.dashboard-content {
  min-height: 600px;
  padding: 30px;
}

.tab-content {
  animation: .4s ease-in-out fadeIn;
  display: none;
}

.tab-content.active {
  display: block;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.overview-grid {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 24px;
  display: grid;
}

.metric-panel, .chart-panel, .achievements-panel, .action-panel, .status-panel {
  background: #fff;
  border: 1px solid #e2e8f0;
  border-radius: 12px;
  padding: 24px;
  transition: all .3s;
  box-shadow: 0 4px 12px #0000000d;
}

.metric-panel:hover, .chart-panel:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px #0000001a;
}

.metrics-grid {
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
  margin-top: 16px;
  display: grid;
}

.metric-card {
  text-align: center;
  background: linear-gradient(135deg, #f8fafc 0%, #f1f5f9 100%);
  border: 1px solid #e2e8f0;
  border-radius: 10px;
  padding: 20px;
}

.metric-value {
  color: #1e293b;
  margin-bottom: 8px;
  font-size: 2.2em;
  font-weight: 700;
}

.metric-label {
  color: #64748b;
  text-transform: uppercase;
  letter-spacing: .5px;
  font-size: .9em;
}

.analytics-grid {
  flex-direction: column;
  gap: 30px;
  display: flex;
}

.analytics-section {
  background: #fff;
  border: 1px solid #e2e8f0;
  border-radius: 12px;
  padding: 30px;
  box-shadow: 0 4px 12px #0000000d;
}

.analytics-controls {
  flex-wrap: wrap;
  gap: 12px;
  margin-bottom: 20px;
  display: flex;
}

.analytics-btn {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%);
  border: none;
  border-radius: 8px;
  padding: 10px 18px;
  font-size: .9em;
  transition: all .3s;
  box-shadow: 0 2px 8px #3b82f64d;
}

.analytics-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 16px #3b82f666;
}

.chart-container {
  background: #f8fafc;
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  margin-top: 20px;
  padding: 20px;
  position: relative;
}

.stats-grid {
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  margin-top: 20px;
  display: grid;
}

.stat-card {
  background: linear-gradient(135deg, #f0f9ff 0%, #e0f2fe 100%);
  border: 1px solid #bae6fd;
  border-radius: 10px;
  padding: 20px;
}

.stat-card h4 {
  color: #0c4a6e;
  margin: 0 0 16px;
  font-size: 1.1em;
}

.wellness-grid {
  grid-template-rows: auto auto;
  grid-template-columns: 1fr 1fr;
  gap: 24px;
  display: grid;
}

.wellness-input-panel {
  background: #fff;
  border: 1px solid #e2e8f0;
  border-radius: 12px;
  grid-row: span 2;
  padding: 30px;
  box-shadow: 0 4px 12px #0000000d;
}

.wellness-form .form-section {
  background: #f8fafc;
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  margin-bottom: 24px;
  padding: 20px;
}

.wellness-form .form-section h4 {
  color: #1e293b;
  margin: 0 0 16px;
  font-size: 1.1em;
}

.wellness-form .input-group {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
  display: flex;
}

.wellness-form label {
  color: #374151;
  flex: 1;
  font-weight: 500;
}

.wellness-form input[type="range"] {
  flex: 2;
  margin: 0 12px;
}

.range-value {
  text-align: center;
  color: #3b82f6;
  min-width: 30px;
  font-weight: 600;
}

.submit-btn {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  border: none;
  border-radius: 8px;
  width: 100%;
  padding: 12px 24px;
  font-size: 1em;
  font-weight: 500;
  transition: all .3s;
}

.submit-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 16px #10b9814d;
}

.wellness-dashboard {
  background: #fff;
  border: 1px solid #e2e8f0;
  border-radius: 12px;
  padding: 24px;
  box-shadow: 0 4px 12px #0000000d;
}

.wellness-scores {
  gap: 16px;
  margin-top: 16px;
  display: flex;
}

.score-card {
  text-align: center;
  background: linear-gradient(135deg, #fef3c7 0%, #fde68a 100%);
  border: 1px solid #f59e0b;
  border-radius: 10px;
  flex: 1;
  padding: 20px;
}

.score-value {
  color: #92400e;
  margin-bottom: 8px;
  font-size: 2em;
  font-weight: 700;
}

.score-label {
  color: #78350f;
  text-transform: uppercase;
  letter-spacing: .5px;
  font-size: .9em;
}

.planning-grid {
  grid-template-rows: auto auto;
  grid-template-columns: 300px 1fr;
  gap: 24px;
  display: grid;
}

.plan-creation-panel {
  background: #fff;
  border: 1px solid #e2e8f0;
  border-radius: 12px;
  grid-row: span 2;
  padding: 30px;
  box-shadow: 0 4px 12px #0000000d;
}

.planning-form .form-group {
  margin-bottom: 20px;
}

.planning-form label {
  color: #374151;
  margin-bottom: 8px;
  font-weight: 500;
  display: block;
}

.planning-form select {
  background: #fff;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  width: 100%;
  padding: 10px 12px;
  font-size: .95em;
}

.generate-btn {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(135deg, #8b5cf6 0%, #7c3aed 100%);
  border: none;
  border-radius: 8px;
  width: 100%;
  padding: 14px 24px;
  font-size: 1em;
  font-weight: 500;
  transition: all .3s;
}

.generate-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 16px #8b5cf64d;
}

.insights-grid {
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 24px;
  display: grid;
}

.ai-insights-panel, .performance-analysis-panel, .predictions-panel, .risk-assessment-panel {
  background: #fff;
  border: 1px solid #e2e8f0;
  border-radius: 12px;
  padding: 30px;
  box-shadow: 0 4px 12px #0000000d;
}

.insights-btn {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(135deg, #06b6d4 0%, #0891b2 100%);
  border: none;
  border-radius: 8px;
  margin-bottom: 8px;
  margin-right: 12px;
  padding: 10px 18px;
  font-size: .9em;
  transition: all .3s;
}

.insights-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 16px #06b6d44d;
}

.insights-content {
  background: #f8fafc;
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  margin-top: 20px;
  padding: 20px;
}

.insight-item {
  background: #fff;
  border-left: 4px solid #3b82f6;
  border-radius: 6px;
  margin-bottom: 8px;
  padding: 12px 16px;
}

.insight-item.warning {
  background: #fffbeb;
  border-left-color: #f59e0b;
}

.insight-item.success {
  background: #ecfdf5;
  border-left-color: #10b981;
}

.insight-item.error {
  background: #fef2f2;
  border-left-color: #ef4444;
}

.notification-area {
  z-index: 1000;
  width: 320px;
  position: fixed;
  top: 20px;
  right: 20px;
}

.notification {
  background: #fff;
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  align-items: center;
  gap: 12px;
  margin-bottom: 12px;
  padding: 16px;
  animation: .3s ease-out slideInRight;
  display: flex;
  box-shadow: 0 8px 24px #00000026;
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(100%);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.notification.success {
  border-left: 4px solid #10b981;
}

.notification.warning {
  border-left: 4px solid #f59e0b;
}

.notification.error {
  border-left: 4px solid #ef4444;
}

.notification.info {
  border-left: 4px solid #3b82f6;
}

.notification-icon {
  font-size: 1.2em;
}

.notification-message {
  color: #374151;
  flex: 1;
  font-size: .9em;
}

.notification-close {
  cursor: pointer;
  color: #9ca3af;
  background: none;
  border: none;
  font-size: 1.2em;
  transition: color .2s;
}

.notification-close:hover {
  color: #374151;
}

@media (width <= 1200px) {
  .overview-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .planning-grid, .wellness-grid {
    grid-template-columns: 1fr;
  }
}

@media (width <= 768px) {
  .dashboard-header {
    flex-direction: column;
    gap: 16px;
  }

  .dashboard-controls {
    justify-content: center;
    width: 100%;
  }

  .tab-navigation {
    flex-wrap: wrap;
    justify-content: center;
  }

  .tab-btn {
    padding: 12px 16px;
    font-size: .9em;
  }

  .dashboard-content {
    padding: 20px;
  }

  .overview-grid, .metrics-grid {
    grid-template-columns: 1fr;
  }

  .wellness-scores {
    flex-direction: column;
  }

  .insights-grid {
    grid-template-columns: 1fr;
  }

  .analytics-controls {
    flex-direction: column;
  }

  .notification-area {
    width: auto;
    left: 20px;
    right: 20px;
  }
}

.control-btn:focus, .tab-btn:focus, .analytics-btn:focus, .submit-btn:focus, .generate-btn:focus, .insights-btn:focus {
  outline-offset: 2px;
  outline: 2px solid #3b82f6;
}

.loading-overlay {
  z-index: 10;
  background: #ffffffe6;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  inset: 0;
}

.loading-spinner {
  border: 4px solid #e2e8f0;
  border-top-color: #3b82f6;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: 1s linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@media (prefers-color-scheme: dark) {
  .advanced-dashboard {
    background: linear-gradient(135deg, #1e293b 0%, #0f172a 100%);
  }

  .metric-panel, .chart-panel, .achievements-panel, .action-panel, .status-panel, .analytics-section, .wellness-input-panel, .wellness-dashboard, .plan-creation-panel, .ai-insights-panel, .performance-analysis-panel, .predictions-panel, .risk-assessment-panel, .notification {
    color: #f1f5f9;
    background: #1e293b;
    border-color: #334155;
  }
}
